import {uniPopup} from "@dcloudio/uni-ui/lib/uni-popup/uni-popup.vue" 
export default {
	data() {  
		return {
		content:"",
		name:"",
		open_time:"",
		shrink:"",
		showPopupMiddleImg:false,
		showPopupMiddleImg1:false,
		details:[],
		id:"",
		collection:1,
		report:"",
		errorMessage:"",
		nodeContent:"",
		}
	},
	onLoad:function(option){ 
		var _self = this
		_self.id = option.id
		_self.getInfo()
	},
	methods: {
		previewImage: function(e) {
			uni.previewImage({
				current: e,
				urls: [e]
			})
		},
		apply:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/activityReport",
				data:{id:_self.id,optionId:uni.getStorageSync("openId"),report:_self.report},
				success:function(res){
					console.log(res)
					if(_self.report==1){
						if(res.data ==2){
							_self.errorMessage = "您尚未进行认证"
							_self.showPopupMiddleImg1 = true
							return false
						}
						else if(res.data ==1){
							_self.errorMessage = "恭喜你,报名成功"
							_self.showPopupMiddleImg = true
							_self.report  = 0
							return false
						}
						else if(res.data == 6){
							uni.showToast({
								title:"活动已暂停",
								icon:"none"
							})
							return false
						}
						else if(res.data == 7){
							uni.showToast({
								title:"活动已结束",
								icon:"none"
							})
							return false
						}
						else if(res.data == 8){
							uni.showToast({
								title:"活动未开始",
								icon:"none"
							})
							return false
						}
						else if(res.data == 99){
							uni.showModal({
								title: '提示',
								content: '此操作需用户授权，是否进行授权',
								success: function (res) {
									if (res.confirm) {
										//跳转到授权页面  
										uni.navigateTo({
											url:"../../login/login"  
										})
										console.log('用户点击确定');
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							});
							return false
						}
					}else if(_self.report===0){
						if(res.data ==2){
							_self.errorMessage = "您尚未进行认证"
							_self.showPopupMiddleImg1 = true
							return false
						}
						if(res.data == 0){
							uni.showToast({
								title:"取消报名失败",
								icon:"none"
							})
							return false
						}
						else if(res.data == 99){
							uni.showModal({
								title: '提示',
								content: '此操作需用户授权，是否进行授权',
								success: function (res) {
									if (res.confirm) {
										//跳转到授权页面  
										uni.navigateTo({
											url:"../../login/login"  
										})
										console.log('用户点击确定');
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							});
							return false
						}
						else if(res.data == 6){
							uni.showToast({
								title:"活动已暂停",
								icon:"none"
							})
							return false
						}
						else if(res.data == 7){
							uni.showToast({
								title:"活动已结束",
								icon:"none"
							})
							return false
						}
						else if(res.data == 8){
							uni.showToast({
								title:"活动未开始",
								icon:"none"
							})
							return false
						}
						else if(res.data==1){
								uni.showToast({
								title:"取消报名成功",
								icon:"none"
							})
							_self.report = 1
							return false
						}
					}
					
					console.log(res.data)
				}
			})
		},
		hidePopup:function(){
			this.showPopupMiddleImg = false
		},
		hidePopup1:function(){
			this.showPopupMiddleImg1 = false
		},
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/activityTubeQuery",
				data:{id:_self.id,optionId:uni.getStorageSync("openId")},
				success:function(res){
					console.log(res)
					_self.details = res.data[0]
					_self.nodeContent = res.data[0].content.replace(/<img/gi, '<img style="max-width:100%;height:auto;display:block" ');
					//用户reply为undefined该用户未报名
					if(res.data[0].reply === undefined || res.data[0].reply===null){
						_self.report =1
					}else{
						_self.report =0
					}
					//用户mark为undefined该用户未收藏
					if(res.data[0].mark === undefined || res.data[0].mark===null){
						_self.collection = 1
					}else{
						_self.collection = 2
					} 
					console.log(_self.report)
				}
			})
		},
		/**
		 * 跳转认证
		 */
		goAudit:function(){
			uni.switchTab({
				url:"../../../pages/mine/mine"
			})
		},
		collect:function(){
			var _self = this
			if(this.collection == 1){
				uni.request({
					url:_self.$api+"dockingManager/activityHideAdd",
					data:{id:_self.id,mark:0,optionId:uni.getStorageSync("openId")},
					success:function(res){
						console.log(res)
						if(res.data==1){
							console.log("收藏成功")
							_self.collection = 2
						}
						else if(res.data==99){
							uni.showModal({
							title: '提示',
							content: '此操作需用户授权，是否进行授权',
							success: function (res) {
								if (res.confirm) {
									//跳转到授权页面  
									uni.navigateTo({
										url:"../../login/login"  
									})
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
						}
						else if(res.data ==98){
								uni.showToast({
								title:"您尚未进行认证，请先进行认证",
								icon:"none"
							})
							return false
						}
						else{
							uni.showToast({
								title:"收藏失败",
								icon:"none"
							})
						} 
					}
				})
			}else{
					uni.request({
					url:_self.$api+"dockingManager/activityHideDelete",
					data:{id:_self.id,optionId:uni.getStorageSync("openId")},
					success:function(res){
						console.log(res)
						if(res.data==1){
							console.log("取消收藏成功")
							_self.collection = 1
						}else if(res.data==99){
							uni.showModal({
								title: '提示',
								content: '此操作需用户授权，是否进行授权',
								success: function (res) {
									if (res.confirm) {
										//跳转到授权页面  
										uni.navigateTo({
											url:"../login/login"  
										})
										console.log('用户点击确定');
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							});
						}
						else{
							uni.showToast({
								title:"取消收藏失败",
								icon:"none"
							})
						}						
					},
					})
			}
		},
				onShareAppMessage: function (options) {
					var _self = this
					if (options.from === 'button') {
					  // 来自页面内转发按钮
					  console.log(options.target)
					}
					return {
					  //## 此为转发页面所显示的标题
					  //title: '好友代付', 
					  //## 此为转发页面的描述性文字
					  desc: _self.details.name, 
					  //## 此为转发给微信好友或微信群后，对方点击后进入的页面链接，可以根据自己的需求添加参数
					  path:  'pages/pageChildren/activity/activity?id='+_self.id, 
					  //## 转发操作成功后的回调函数，用于对发起者的提示语句或其他逻辑处理
					  success: function(res) {
						  uni.request({
						  url:_self.$api+"dockingManager/activityHideAdd",
						  data:{id:_self.id,mark:1,optionId:uni.getStorageSync("openId")},
						  success:function(res){
							  console.log(res)
							  	console.log("转发成功")
						  },
						  }) 
					  },
					  //## 转发操作失败/取消 后的回调处理，一般是个提示语句即可
					  fail: function() {
						  console.log("error")
					  }
					}
			},
	},
	 components: {uniPopup},
	 filters:{
		 spliceData:function(newData){
			  return newData.replace(/\<img/gi,   '<img class="rich-img" ' );
		 },
		 pngJpg:function(data){
			
		 },
		 getpic:function(data){
			 if(data == undefined){
				 return ''
			 }else{
				 return data.split(',')[0]
			 }
		 }
	 }
	
}
